<template>
  <div class="picture">
    <slot></slot>
    <div class="picture__container">
      <img class="picture__img" v-bind:src="value">
    </div>
  </div>
</template>

<script>
export default {
  props: ["value"]
};
</script>


<style lang="scss">
  .picture{
    

    &__container{
      padding: 10px 0;
      margin-bottom: 0px;  
      margin-top: -21px;
      position: relative;
    }

    &__img{
      max-width: 100%;
      height: auto;
      display: block;
      position: absolute;
      display: none;
      right: 0;
      top: 0px;
      pointer-events:none;
      z-index: 100;
      padding: 5px;
      background: #fff;
      box-sizing: border-box;
    }

    &:hover{
      .picture__img{
        display: block;
      }
    }
  }
</style>



