<template>
  <div class="create">
    <input type="text" class="input create__input" placeholder="Newsletter Name" v-model="name" v-if="opened" transition>
    <button class="button" v-on:click="create" v-bind:class="{'create__button' : opened}">New</button>
  </div>
</template>

<script>
import { createNewsletter } from "../../vuex/actions";

export default {
  data () {
    return {
      opened: false,
      name: ""
    };
  },

  vuex: {
    actions: { createNewsletter }
  },

  methods: {
    create () {
      if (this.opened && this.name) {
        this.createNewsletter(this.name);
        this.opened = false;
      } else {
        this.name = "";
        this.opened = true;
      }
    }
  }
};
</script>

<style lang="scss">
  @import "../../assets/scss/button.scss";

  .create{
    position: relative;
    width: 100%;
    display: flex;

    &__button{
      border-radius: 0 5px 5px 0;
      width: 30%;
    }

    &__input{
      width: 75%;
    }

    >*{
      transition: width .25s;

      &.v-enter, &.v-leave{
        width: 0;
      }
    }
  }
  
</style>



