<template>
  <section id="open">
    <create></create>
    <ul class="newsletters-list">
      <li class="newsletters-list__item" v-for="newsletter in newsletters">
        <span v-link="'editor/' + newsletter.id">{{ newsletter.name }}</span>
        <span class="trash-button" v-on:click="deleteNewsletter(newsletter.id)"><img src="../../assets/icons/trash.svg" width="15"></span>
      </li>
    </ul>
  </section>
</template>

<script>
import { getNewsletters, deleteNewsletter, createNewsletter } from "../../vuex/actions";
import create from "./create";

export default {
  vuex: {
    state: {
      newsletters: state => state.newsletters
    },
    actions: { getNewsletters, deleteNewsletter, createNewsletter }
  },

  components: { create },

  route: {
    data ({ to }) {
      this.getNewsletters();
    }
  }
};
</script>

<style lang="scss">
  @import "../../assets/scss/button.scss";

  #open{
    margin: 0px auto;
    box-sizing: border-box;
    padding: 30px;
  }

  .trash-button{
    float: right;
    display: none;
  }

  .newsletters-list{
    border-radius: 2px 2px 0 0;
    border: 1px solid #eee;
    border-bottom: none;
    list-style: none;
    margin: 20px 0;
    padding: 0;
    

    &__item{
      color: #666;
      padding: 0;
      margin: 20px 0;
      cursor: pointer;
      
      &:last-child{
        border-bottom: none;
      }

      &:hover .trash-button{
        display: block;
      }
    }
  }
</style>



