import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const state = {
  newsletters: [],
  newsletter: {},
  version: -1
};

const mutations = {
  NEWSLETTERS_RECIEVED (state, newsletters) {
    state.newsletters = newsletters;
  },

  DELETED_NEWSLETTER (state, name) {
    state.newsletters.$remove(name);
  },

  NEWSLETTER_RECIEVED (state, newsletter) {
    state.newsletter = newsletter;
  },

  NEWSLETTER_SAVED (state, version) {
    state.version = version;
  },

  NEWSLETTER_CREATED (state, id) {
    state.id = id;
  }

};

export default new Vuex.Store({
  state,
  mutations
});



