<template>
  <div class="text-field">
    <label v-if="label" class="text-field__label" :for="_uid">{{label}}:</label>
    <input v-if="!multiline" class="text-field__input" v-model="value" type="text" :id="_uid"></input>
    <textarea v-if="multiline" class="text-field__input" v-model="value" rows="9" :id="_uid"></textarea>
  </div>
</template>

<script>
export default {
  props: ["value", "label", "multiline", "inline"]
};
</script>


<style lang="scss">
  @import "../../../../assets/scss/ui";

  .text-field{
    &__label{ @extend .label; }
    &__input{ @extend .input; }

    margin-bottom: 20px;
  }
</style>



