<template>
  <section class="featured">
    <text-field label="Section title" v-bind:value.sync="content.title"></text-field>
    <book :label="'Book ' + $index" v-bind:value.sync="book" v-for="book in content.items"></book>
    <cta label="Call to action button" v-bind:value.sync="content.cta"></cta>
  </section>
</template>

<script>
import textField from "./ui/text-field";
import book from "./ui/book";
import cta from "./ui/cta";

export default {
  props: ["content"],
  components: {
    textField, book, cta
  }
};
</script>


