<template>
  <div class="cta">
    <label v-if="label" class="cta__label">{{label}}:</label>
    <div class="cta__fields">
      <text-field v-bind:value.sync="value.label" :inline="true"></text-field>
      <text-field v-bind:value.sync="value.url" :inline="true"></text-field>
    </div>
  </div>
</template>

<script>
import textField from "./text-field";
import picture from "./picture";
import enabler from "./enabler";

export default {
  props: ["value", "label"],
  components: {
    textField, picture, enabler
  }
};
</script>


<style lang="scss">
  @import "../../../../assets/scss/ui";

  .cta{    
    &__fields{
      >* {margin-bottom: 5px};
    }

    &__label{
      @extend .label;
    }
  }
</style>



