<template>
  <section class="destination">
    <text-field label="Destination title" v-bind:value.sync="content.title"></text-field>
    <text-field label="Destination content" v-bind:value.sync="content.content" :multiline="true"></text-field>
    <picture v-bind:value.sync="content.image">
      <text-field label="Destination picture" v-bind:value.sync="content.image"></text-field>
    </picture>
    <cta label="Call to action button" v-bind:value.sync="content.cta"></cta>
  </section>
</template>

<script>
import textField from "./ui/text-field";
import picture from "./ui/picture";
import person from "./ui/person";
import cta from "./ui/cta";

export default {
  props: ["content"],
  components: {
    textField, picture, person, cta
  }
};
</script>


