import Vue from "vue";

export const getNewsletters = ({ dispatch }) => {
  Vue.http.get("newsletters").then(function (response) {
    dispatch("NEWSLETTERS_RECIEVED", response.data);
  });
};

export const createNewsletter = ({ dispatch }, name) => {
  Vue.http.post("newsletters", { name: name }).then(function (response) {
    getNewsletters({dispatch});
  });
};

export const deleteNewsletter = ({ dispatch }, id) => {
  Vue.http.delete("newsletters/" + id).then(function (response) {
    getNewsletters({dispatch});
  });
};

export const getNewsletter = ({ dispatch }, id) => {
  Vue.http.get("newsletters/" + id).then(function (response) {
    dispatch("NEWSLETTER_RECIEVED", response.data);
  });
};

export const saveNewsletter = ({ dispatch }, newsletter) => {
  newsletter.version = (newsletter.version === undefined) ? 0 : newsletter.version + 1;

  Vue.http.put("newsletters/" + newsletter.id, { content: newsletter }).then(function (response) {
    dispatch("NEWSLETTER_SAVED", newsletter.version);
  });
};



