<template>
  <section id="editor">
    <nav class="nav">
      <a v-link="'/'"><img src="../../assets/icons/back.svg" width="16"></a>
      <span class="spacer">&nbsp;</span>
      <a v-on:click="saveNewsletter(newsletter)">Save</a>
      <a v-on:click="exportNewsletter(newsletter)"><img src="../../assets/icons/export.svg" width="16"></a>
    </nav>
    <div class="sidebar-blocks">
      <div v-for="block in newsletter.blocks">
        <accordeon v-if="block.type == 'header'" title="Newsletter Header">
          <newsletter-header v-bind:content.sync="block"></newsletter-header>
        </accordeon>
        <accordeon v-if="block.type == 'articles'" title="Articles">
          <articles v-bind:content.sync="block"></articles>
        </accordeon>
        <accordeon v-if="block.type == 'destination'" title="Destination">
          <destination v-bind:content.sync="block"></destination>
        </accordeon>
        <accordeon v-if="block.type == 'featured_titles'" title="Books">
          <featuredc v-bind:content.sync="block"></featuredc>
        </accordeon>
      </div>
    </div>
  </section>
</template>

<script>
import {deleteNewsletter, saveNewsletter, getNewsletter} from "../../vuex/actions";
import textField from "./blocks/ui/text-field";
import accordeon from "./blocks/ui/accordeon";

import newsletterHeader from "./blocks/newsletter-header";
import articles from "./blocks/articles";
import destination from "./blocks/destination";
import featuredc from "./blocks/featured";

export default {
  vuex: {
    state: {
      newsletter: state => state.newsletter
    },
    actions: { deleteNewsletter, saveNewsletter, getNewsletter }
  },

  components: { textField, accordeon, newsletterHeader, articles, destination, featuredc },

  route: {
    data ({ to }) {
      this.getNewsletter(to.params.newsletter);
    }
  }
};
</script>

<style lang="scss">
  #editor{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    display: flex;
  }

  .nav{
    padding: 0px;
    border-bottom: 1px solid #c2c2c2;
    z-index: 1000;
    width: 100%;
    height: 56px;
    display: flex;

    a{
      float: left;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 56px;
      height: 56px;
      cursor: pointer;
      color: #287bbb;
      font-weight: bold;
      text-transform: uppercase;
      font-size: 13px;
      line-height: 1;

      &:hover{
        background: #c9e3f4;
      }
    }

    .spacer{
      flex-grow: 10;
    }
  }

  .sidebar-blocks{
    position: absolute;
    top: 56px;
    left: 0;
    width: 100%;
    bottom: 0;
    overflow: hidden;
    box-sizing: border-box;
    overflow-y: scroll;

  }
</style>



