<template>
  <section class="header">
    <text-field label="Newsletter title" v-bind:value.sync="content.title"></text-field>
    <text-field label="Newsletter subtitle" v-bind:value.sync="content.subtitle"></text-field>
    <picture v-bind:value.sync="content.image">
      <text-field label="Newsletter main picture" v-bind:value.sync="content.image"></text-field>
    </picture>
    <person label="Hero" v-bind:value.sync="content.hero"></person>
    <person label="Sponsor" v-bind:value.sync="content.sponsor"></person>
    <text-field label="Introduction text" v-bind:value.sync="content.content" :multiline="true"></text-field>
    <cta label="Call to action button" v-bind:value.sync="content.cta"></cta>
  </section>
</template>

<script>
import textField from "./ui/text-field";
import picture from "./ui/picture";
import person from "./ui/person";
import cta from "./ui/cta";

export default {
  props: ["content"],
  components: {
    textField, picture, person, cta
  }
};
</script>


