<template>
  <iframe id="preview" v-bind:src="previewURL" :refresh="refresh"></iframe>
</template>

<script>
import Vue from "vue";

export default {
  vuex: {
    state: {
      newsletter: state => state.newsletter,
      version: state => state.version
    }
  },

  computed: {
    previewURL: function () {
      return (this.newsletter.id) ? Vue.http.options.root + "/newsletters/" + this.newsletter.id + "/preview" : "";
    },

    refresh: function () {
      if (this.$el.contentWindow) {
        this.$el.contentWindow.postMessage("refresh", "*");
      }

      return this.version;
    }
  }
};
</script>

<style lang="scss">
  iframe{
    border: none;
    height: 100%;
    flex-grow: 2;
  }
</style>



