<template>
  <div class="accordeon" v-bind:class="{'accordeon__opened':opened}">
    <h2 class="accordeon__title" v-on:click="toggle" >{{title}}</h2>
    <div class="accordeon__content" v-if="opened">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: ["title"],
  data () {
    return {
      opened: false
    };
  },

  methods: {
    toggle () {
      this.opened = !this.opened;
    }
  }
};
</script>


<style lang="scss">
  @import "../../../../assets/scss/ui";

  .accordeon{
    &__title{
      background: #287bbb;
      color: #fff;
      cursor: pointer;
      font-size: 13px;
      font-weight: 600;
      padding: 25px 20px 22px 25px;
      text-transform: uppercase;
      margin: 0 0 2px;
      
      
      &:hover{
        background: #287bbb + 10; 
      }

      &:before{
        content: "+ ";
        display: inline-block;
        width: 12px;
      }
    }

    &__content{
      padding: 20px 30px 30px;
    }
  }

  .accordeon__opened > .accordeon__title:before{
    content: "- ";
  }

  .accordeon .accordeon .accordeon__content{
    padding: 20px 0 30px;
  }

  .accordeon .accordeon .accordeon__title{
    background: none;
    padding: 5px 0;
    color: #287bbb;
    
  }
</style>



