<template>
  <section id="editor">
    <div class="sidebar">
      <router-view class="view" keep-alive transition transition-mode="out-in"></router-view>
    </div>
    <preview></preview>
  </section>
</template>

<script>
import preview from "./editor/preview";
import {getNewsletters} from "../vuex/actions";

export default {
  components: {
    preview
  },

  vuex: {
    actions: {getNewsletters}
  }
};
</script>

<style lang="scss">
  #editor{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    display: flex;
  }

  .sidebar{
    margin-right: 10px;
    border-right: 1px solid #ddd;
    width: 400px;
    position: relative;
    background: #e9f2f8;
  }

  .view{
    transition: opacity .25s;

    &.v-enter, &.v-leave{
      opacity: 0;
    }
  }
</style>



