<template>
  <div class="person">
    <enabler v-bind:enabled.sync="value.enabled">{{label}}:</enabler>
    <div class="person__fields">
      <text-field v-bind:value.sync="value.name" :inline="true"></text-field>
      <text-field v-bind:value.sync="value.subtitle" :inline="true"></text-field>
      <picture v-bind:value.sync="value.image">
        <text-field v-bind:value.sync="value.image" :inline="true"></text-field>
      </picture>
    </div>
  </div>
</template>

<script>
import textField from "./text-field";
import picture from "./picture";
import enabler from "./enabler";

export default {
  props: ["value", "label"],
  components: {
    textField, picture, enabler
  }
};
</script>


<style lang="scss">
  @import "../../../../assets/scss/ui";

  .person{    
    &__fields{
      >* {margin-bottom: 5px};
    }

    &__label{
      @extend .label;
    }
  }
</style>



